.btn {
  display: inline-block;
  width: 100%;
  max-width: 12.625rem;
  color: #fff;
  background-color: #de786b;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  line-height: 1.5714285714;
  padding: 0.95rem 0;
  position: relative;
  border: 1px solid #de786b;
  transition: background-color 0.3s ease;
  z-index: 1;
  text-align: center;
}

.btn:hover {
  color: #de786b;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #fff;
  transition: width 0.3s ease-in-out;
  z-index: -1;
}

.btn:hover::before {
  color: #de786b;
  background-color: #fff;
  width: 100%;
}

.btn::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  right: -4px;
  bottom: -4px;
  border: 1px solid #de786b;
  z-index: -2;
}

.btn span {
  position: relative;
  padding: 0 3rem 0 0;
  text-transform: capitalize;
}

.btn span::after {
  display: inline-block;
  position: absolute;
  content: "";
  background-image: url("../images/common/btn-arrow.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  width: 2.5rem;
  height: 100%;
  top: 45%;
  transform: translateY(-50%);
  right: 0;
}

.btn:hover span::after {
  background-image: url("../images/common/btn-arrow-blue.svg");
}

.btn.btn--sub-404 {
  color: #de786b;
  background-color: #fff;
}

.btn.btn--sub-404:hover {
  color: #fff;
}

.btn.btn--sub-404::before {
  background-color: #de786b;
}

.btn.btn--sub-404:hover::before {
  background-color: #de786b;
  color: #fff;
}

.btn.btn--sub-404::after {
  border: 1px solid #fff;
}

.btn.btn--sub-404 span::after {
