input[type=radio]:checked + .wpcf7-list-item-label::after {
  /* ラジオチェック  */
  content: "";
  position: absolute;
  border: solid #C94800;
  border-width: 0 2px 2px 0;
  left: 0.25rem;
  top: 0.0625rem;
  width: 0.375rem;
  height: 0.625rem;
  transform: rotate(45deg);
}

/* チェックボックス */
input[type=checkbox] {
  opacity: 0; /* デフォルトのチェックボックスを見えなくする */
  position: absolute;
}

input[type=checkbox] + .wpcf7-list-item-label::before {
  /* チェックボックス */
  position: absolute;
  top: -0.25rem;
  left: -2.5rem;
  display: block;
  height: 1.375rem;
  width: 1.375rem;
  border: solid 0.125rem #de786b;
  box-sizing: border-box;
  margin-right: 0.625rem;
  margin-top: 0.125rem;
  flex-shrink: 0;
}
