  input[type=checkbox] + .wpcf7-list-item-label::before {
    margin-right: 1rem;
  }
}

input[type=checkbox]:checked + .wpcf7-list-item-label::after {
  /* チェック（チェックボックス）  */
  content: "";
  position: absolute;
  border: solid #C94800;
  border-width: 0 2px 2px 0;
  left: -2.125rem;
  top: -0.125rem;
  width: 0.625rem;
  height: 1rem;
  transform: rotate(45deg);
}

